<template>
  <div class="w-100">
    <!-- Agent profile and name block starts here -->
    <div class="d-flex card-body px-4 pt-3 pb-0 mt-3" v-if="from == 'agent'">
      <b-img :src="profile.pic_path" v-if="profile.pic_path" alt="Agent profile" height="35" width="35" class="rounded" />
      <h5 class="first-letter-caps font-size-base mb-0 ml-3 text-truncate text-white opacity-0-8">{{ profile.name }}</h5>
    </div>
    <!-- Agent profile and name block ends here -->
    <template v-if="from == 'visitor'">
      <b-button
        :style="style_call_now"
        variant="outline"
        class="mt-3 ml-3 call-now"
        @click="
          $emit('visitor_click', 'call_now');
          updateStyle('callnow');
        "
        ><span class="fe fe-airplay mr-2 align-middle"></span>Call from Computer</b-button
      >
      <b-button
        :style="style_call_back"
        variant="outline"
        class="ml-3 mt-3 call-back"
        @click="
          $emit('visitor_click', 'call_back');
          updateStyle('callback');
        "
        ><span class="fe fe-phone-forwarded mr-2 align-middle"></span>Call Back</b-button
      >
    </template>
  </div>
</template>

<script>
  import call from "../../js/dialer/call";
  export default {
    props: {
      from: String,
      widgetData: Object,
    },
    data() {
      return {
        profile: {},
        show_form: false,
        style_call_now: {},
        style_call_back: {},
      };
    },
    mounted: async function() {
      if (this.from != "visitor") {
        this.profile = await call.getProfile();
        if (!this.profile.pic_path) this.profile.pic_path = "/img/default_avatar.png";
      }
        if(this.widgetData.bgcolor){
        this.style_call_now = { background: this.widgetData.bgcolor, borderColor: this.widgetData.bgcolor, color: "white" };
      }
      else{
        setTimeout(()=>{
          this.updateStyle('callnow');
        },300);
      }
    },
    methods: {
      // Update button and text based on actions
      updateStyle(type) {
        if (type == "callnow") {
          this.style_call_now = { background: this.widgetData.bgcolor, borderColor: this.widgetData.bgcolor, color: "white" };
          this.style_call_back = { borderColor: this.widgetData.bgcolor2, color: this.widgetData.bgcolor2 };
          return;
        }
        this.style_call_now = { borderColor: this.widgetData.bgcolor, color: this.widgetData.bgcolor };
        this.style_call_back = { background: this.widgetData.bgcolor2, borderColor: this.widgetData.bgcolor2, color: "white" };
      },
    },
  };
</script>
<style>
  .first-letter-caps::first-letter {
    text-transform: capitalize;
  }
</style>
